import React from 'react';
import { Button } from './Button.js';
import './styles/ProjectCard.css';


function ProjectCard(props) {
  const suspensivePot = input => input.length > 65 ? `${input.substring(0,65)}...` : input;

  return <div className="ProjectCard">
    <div className='ProjectCard__container' tabIndex="0">
      <img className='ProjectCard__thumbnail' src='https://www.fundssociety.com/sites/default/files/styles/355x213/public/2021-02/r2d2-933066_1920.jpg?itok=KU-X3ZEe' alt="Portada de Proyecto"/>
      <div className='ProjectCard__body'>
        <h2 className='ProjectCard__title'>{props.projectTitle}</h2>
        <p className='ProjectCard__description'>{suspensivePot(props.description)}</p>
      </div>
      <div className="ProjectCard__footer">
        <Button buttonSize='mobile' buttonStyle='secondary'>Ver Proyecto</Button>
        <span><a className='ProjectCard__repo-link' href="http://github.com" target="_blank" rel="noreferrer">Ver repositorio</a></span>
      </div>
    </div>
  </div>
}

export default ProjectCard;