import React from 'react';
import { ImgLogo, LetterLogo } from '../Icons';
import './styles/LogoIcon.css';

const SIZES = ['small', 'large'];

const LogoIcon = ({ size }) => {
  const checkSize = SIZES.includes(size) ? size : SIZES[0];

  return <div className="Logo">
    <ImgLogo className='Logo__logo' />
    {checkSize !== 'small' && <LetterLogo className='Logo__letter' />}
  </div>
} 

export default LogoIcon;
