import React, { useState } from 'react';
import { Link, NavLink } from 'react-router-dom';
import LogoIcon from './LogoIcon';
import { Bars, Close } from '../Icons';

import './styles/Navbar.css';

function Navbar() {
  const [click, setClick] = useState(false);
  
  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  return <nav className={click ? 'Navbar Navbar--fixed' : 'Navbar'}>
      <div className='Navbar__container'>
        <Link to="/" onClick={closeMobileMenu}>
          <LogoIcon size='large' color='black'/>
        </Link>
        <button className="Navbar__switch" onClick={handleClick}>
            {click ? 
              <Close className="Navbar__icon" /> :
              <Bars className="Navbar__icon"/>
            }
          </button>
          <ul className={click ? 'Nav-menu Nav-menu--active' : 'Nav-menu'}>
            <li className='Nav-menu__item' title="Home">
              <NavLink to='/' exact className='Nav-menu__links' activeClassName='Nav-menu__links--active' onClick={closeMobileMenu}>
                Inicio
              </NavLink>
            </li>
            <li className='Nav-menu__item' title="Blogs">
              <NavLink to='/blogs' className='Nav-menu__links' activeClassName='Nav-menu__links--active' onClick={closeMobileMenu}>
                Blogs
              </NavLink>
            </li>
            <li className='Nav-menu__item' title="Projects">
              <NavLink to='/projects' className='Nav-menu__links' activeClassName='Nav-menu__links--active' onClick={closeMobileMenu}>
                Proyectos
              </NavLink>
            </li>
            <li className='Nav-menu__item' title="About me">
              <NavLink to='/about' className='Nav-menu__links' activeClassName='Nav-menu__links--active' onClick={closeMobileMenu}>
                Sobre me
              </NavLink>
            </li>
          </ul>
        </div>
  </nav>
}

export default Navbar;
